<template>
  <div>
    <div class="chart-box">
      <el-row :gutter="45">
        <el-col :span="6" :offset="3">
          <el-card class="charBox">
            <div class="count1"><span>{{ CountOne }}</span></div>
            <div class="a"></div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="charBox">
            <div class="count2"><span>{{ CountTwo }}</span></div>
            <div class="b">
              API
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="charBox">
            <div class="count3"><span :key="new Date().getTime()">{{ getRunnedTime }}</span></div>
            <div class="b">
              
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-card id="apiIncrease">
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card id="userIncrease">
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {CountApi} from "@/core/request/api/CountApi";

export default {
  name: "dataPage",
  data() {
    return {
      CountOne: '',
      CountTwo: '',
      countRunning: null,
      authColumns: [],
      authValues: [],
      userColumns: [],
      userValues: [],
    }
  },
  mounted() {
    this.getincreaseCount(),
    this.getGraphIncDataWithUser(),
    this.getUserCount(),
    this.getHomeCount()
    this.getRunningTime()
  },
  computed: {
    getRunnedTime() {
      var old_date = new Date(this.countRunning); //"yyyy-MM-dd HH:mm:ss"
      // eslint-disable-next-line no-constant-condition
      var new_date = new Date(); //
      var difftime = (new_date - old_date) / 1000; //,
      var days = parseInt(difftime / 86400); //   24*60*60*1000
      var hours = parseInt(difftime / 3600) - 24 * days;    //  60*60 -=
      var minutes = parseInt(difftime % 3600 / 60); //  -(day*24) 60   /60 
      var seconds = parseInt(difftime % 60);  // 60  
      return (days + "" + hours + "" + minutes + "" + seconds + "");

    }
  },
  methods: {

    getUserCount() {
      this.axios.get(CountApi.userCount).then(res => {
        if (res.data.code !== 0) return this.$message.error('')
        this.CountOne = res.data.data
        // console.log(res)
      })
    },
    getRunningTime() {
      this.axios.get(CountApi.runTime).then(res => {
        if (res.data.code !== 0) return this.$message.error('')
        this.countRunning = res.data.data
        // console.log(res)
      })
    },
    getHomeCount() {
      this.axios.get(CountApi.docCount).then(res => {
        if (res.data.code !== 0) return this.$message.error('')
        this.CountTwo = res.data.data
      })
    },
    getincreaseCount() {
      this.axios.get(CountApi.increaseCount, {
        params: {
          day: 7,
        }
      }).then(res => {
        if (res.data.code !== 0) return this.$message.error('')
        this.authColumns = res.data.data.columns
        this.authValues = res.data.data.values
        this.setApiGraph()
        // console.log(res)
      })
    },
    getGraphIncDataWithUser() {
      this.axios.get(CountApi.userIncCount, {
        params: {
          day: 7,
        }
      }).then(res => {
        if (res.data.code !== 0) return this.$message.error('')
        this.userColumns = res.data.data.columns
        this.userValues = res.data.data.values
        this.setUserGraph()
      })
    },
    setApiGraph() {
      // domecharts
      const apiIncrease = echarts.init(document.getElementById('apiIncrease'));
      const option = {
        title: {
          text: 'API',
          left: 'center',
          top: '5px',
        },
        toolbox: {
          // 
          show: true, // 
          feature: {
            // 
            mark: {show: true}, // 
            dataView: {show: true, readOnly: false}, // 
            magicType: {show: true, type: ['bar']}, // 
            restore: {show: true}, // 
            saveAsImage: {show: true} // 
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            }
          },
        },
        xAxis: {
          name: '',
          type: 'category',
          data: this.authColumns
        },
        yAxis: {
          name: '',
          type: 'value'
        },
        series: [
          {
            name: '',
            data: this.authValues,
            type: 'line'
          }
        ]
      };
      apiIncrease.setOption(option)
    },
    setUserGraph() {
      // domecharts
      const userIncrease = echarts.init(document.getElementById('userIncrease'));
      const option = {
        title: {
          text: '',
          left: 'center',
          top: '5px',
        },
        toolbox: {
          // 
          show: true, // 
          feature: {
            // 
            mark: {show: true}, // 
            dataView: {show: true, readOnly: false}, // 
            magicType: {show: true, type: ['bar']}, // 
            restore: {show: true}, // 
            saveAsImage: {show: true} // 
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            }
          },
        },
        xAxis: {
          name: '',
          type: 'category',
          data: this.userColumns
        },
        yAxis: {
          name: '',
          type: 'value'
        },
        series: [
          {
            name: '',
            data: this.userValues,
            type: 'line'
          }
        ]
      };
      userIncrease.setOption(option)
    }
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}

.count1 {
  position: relative;
  font-style: normal;
  font-size: 70px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.count2 {
  font-style: normal;
  font-size: 70px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.count3 {
  font-style: normal;
  font-size: 25px;
  font-family: 'Arial';
  color: #83a2ed;
  line-height: 120px;
  text-align: center;
}

.charBox .a {
  font-style: normal;
  font-size: 29px;
  font-family: 'Arial';
  background-color: #ffffff;
  color: #83a2ed;
  bottom: 0px;
  text-align: center;
}

.charBox .b {
  font-style: normal;
  font-size: 29px;
  font-family: 'Arial';
  background-color: #ffffff;
  color: deepskyblue;
  bottom: 0px;
  text-align: center;
}

#apiIncrease {
  width: 550px;
  height: 250px;
  margin: 35px auto;
  border-radius: 20px;
  border: 1px solid #83a2ed;
}

#userIncrease {
  width: 550px;
  height: 250px;
  margin: 35px auto;
  border-radius: 20px;
  border: 1px solid #83a2ed;
}
</style>
