<template>
  <div class="mannggerMain">

    <el-container>
      <el-header>
        <el-card>
          <el-breadcrumb separator="/" :key="new Date().getTime()">
            <el-breadcrumb-item v-for="(item,index) in breadcrumb" :key="index" :to="{ path: item.path}">
              {{item.meta.title}}
            </el-breadcrumb-item>
          </el-breadcrumb>
        </el-card>
      </el-header>
      <el-main>
        <el-card><router-view></router-view></el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "mannggerMain",
  data(){
    return{
      routerName:null,
      routerDir:null
    }
  },
  computed:{
    // 
    breadcrumb(){
      // metashowInbreadcrumb
      let matchedArr = this.$route.matched.filter((item)=>{
            return item.meta.showInbreadcrumb
          }
      );
      // 
      if(matchedArr[0].meta.title !== ''){
        matchedArr.unshift(
            {
              path:'/',
              meta:{
                showInbreadcrumb:true,
                title:""
              }
            },
        )
      }
      return matchedArr;
    }
  },
  methods:{

  },
  mounted() {

  }
}
</script>

<style scoped>
*{
  margin:0;
  padding: 0;
}
</style>